                select(@change="onChange()" v-model="selectedDepartment").programmes__filter-select
                    option(value='') Все
                    option(:value='item.name' v-for='item in departments') {{item.name}}
            .programmes__filter-item
                label.label__filter Выберите автора:
                select(v-model="selectedAuthor").programmes__filter-select
                    option(value='') Все
                    template(v-for='department in departments' v-if="selectedDepartment===department.name")
                        option(:value='author' v-for='author in department.teachers.slice().sort()'  v-if="selectedDepartment!==''") {{author}}
                    template(v-for='department in departments'  v-if="selectedDepartment===''")
                        option(:value='item' v-for='item in department.teachers.slice().sort()') {{item}}
            .programmes__filter-item
                label.label__filter Выберите предмет:
                select(v-model="selectedSubject").programmes__filter-select
                    option(value='') Все
                    template(v-for='department in departments' v-if="selectedDepartment===department.name")
                        option(:value='subject' v-for='subject in department.subjects.slice().sort()'  v-if="selectedDepartment!==''") {{subject}}
                    template(v-for='department in departments'  v-if="selectedDepartment===''")
                        option(:value='item' v-for='item in department.subjects') {{item}}
            .programmes__filter-item
                label.label__filter(for='classes') Выберите класс:
                select(name='classes' v-model="selectedClass").programmes__filter-select
                    option(:value='0') Все
                    option(:value='item' v-for='item in 12') {{item}}
            .programmes__filter-item
                label.label__filter(for='languages') Выберите язык:
                select(v-model="selectedLanguage").programmes__filter-select
                    option(value='') Все
                    option(value='Казахский') Казахский
                    option(value='Русский') Русский
        ul.programmes__list
            li.programmes__item(v-for="programme,idx in filteredProgrammes" :key="programme.id")
                programme-item(
                    :index='idx'
                    :programme='programme'
                )
        button(@click.prevent='addProgramme' id='addProgramme').add__programme Добавить программу
        add-programme-form(
            v-if='addProgrammeFormVisible'
            v-on:eventNewProgramme='eventNewProgramme'
            )
