                    }
                ]
            },
            
        ]
    }
  },   
  computed: {
        filteredProgrammes: function() {
            return this.programmes
            //filter by department
            .filter(programme => {
                return this.selectedDepartment == '' || programme.programmeDepartment == this.selectedDepartment;
            })
            //filter by teacher
            .filter(programme => {
                return this.selectedAuthor == '' || programme.programmeAuthor == this.selectedAuthor;
            })
            //filter by subject
            .filter(programme => {
                return this.selectedSubject == '' || programme.programmeSubject== this.selectedSubject;
            })
            //filter by class
            .filter(programme => {
                return this.selectedClass == 0 || programme.programmeClass== this.selectedClass;
            })
            //filter by language
            .filter(programme => {
                return this.selectedLanguage == 0 || programme.programmeLanguage== this.selectedLanguage;
            })
        }
    },   
  methods: {
      addProgramme() {
          this.addProgrammeFormVisible=!this.addProgrammeFormVisible
          document.getElementById('addProgramme').setAttribute("disabled", "true")
      },
      eventNewProgramme() {
          this.addProgrammeFormVisible=!this.addProgrammeFormVisible
          document.getElementById('addProgramme').removeAttribute("disabled")
      },
      onChange() {
            this.selectedSubject = ''
            this.selectedAuthor = ''
      }
  }
}
</script>
